import navigation from "~/components/navigation";
import stickyFooter from "@/components/stickyFooter";
export default {

  components: {navigation,stickyFooter},
  head: {
    title: 'c1chy.web',
    meta: [
      {charset: 'utf-8'},
      {name: 'viewport', content: 'width=device-width, initial-scale=1'},
      {
        hid: 'Portfolio',
        name: 'c1chy.de',
        content: 'My Frontend experiences'
      }
    ],
    link: [{rel: 'icon', type: 'image/x-icon', href: '/favicon.ico'}]
  },

  layout: 'desktop',

 transition: {
    name: 'shutters',
    mode: 'out-in',

  },


  data() {


    return {

      fade:false,
      show:false,
      animate: false,
      options: {
        ref: true,
        dragAndMove: true,
        scrollingSpeed: 1500,
        afterLoad: this.afterLoad,
        navigation: true,
        anchors: ['page1', 'page2', 'page3', 'page4'],

        sectionsColor: ['#40E0D0', '#e4ddd3', '#1c1716', '#fec401'],
        controlArrows: true,
        autoScrolling: true,
        animateAnchor: true,
        passive: true,
        verticalCentered: false,
        lazyLoading: true,
      },
    }
  },

  methods: {

    afterLoad: function (origin, destination, direction) {

      this.animate = false
      this.fade = false
      this.show = false

      if (destination.index === 1 || destination.index === 2 || destination.index === 3  ) {

        this.animate = true
        this.fade = true
        this.show = true
        console.log(this.animate);



      }


      console.log("After load....");
      console.log(destination.index);

/*    var fullPage = document.getElementsByClassName('fp-enabled')*/


    },


    getFormValues (submitEvent) {
      this.name = submitEvent.target.elements.name.value
    },




 /*   toTop() {


      fullpage_api.moveTo('page1',2)

    }*/
},

  mounted() {

    fullpage_api.reBuild('all')


  }
}

